module $ { export class $mol_rower_demo extends $mol_rower {

	/// heightMinimal 50
	heightMinimal() {
		return 50
	}

	/// helloHint @ \Title
	helloHint() {
		return this.text( "helloHint" )
	}

	/// title \
	@ $mol_prop()
	title( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : ""
	}

	/// suggest1 @ \Hello
	suggest1() {
		return this.text( "suggest1" )
	}

	/// suggest2 @ \World
	suggest2() {
		return this.text( "suggest2" )
	}

	/// titler $mol_suggester 
	/// 	hint < helloHint 
	/// 	value > title 
	/// 	suggests / 
	/// 		< suggest1 
	/// 		< suggest2
	@ $mol_prop()
	titler( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_suggester().setup( __ => { 
			__.hint = () => this.helloHint()
			__.value = ( ...diff : any[] ) => this.title(  ...diff )
			__.suggests = () => [].concat( this.suggest1() , this.suggest2() )
		} )
	}

	/// countHint @ \Count
	countHint() {
		return this.text( "countHint" )
	}

	/// count null
	@ $mol_prop()
	count( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : <any> null
	}

	/// counter $mol_number 
	/// 	hint < countHint 
	/// 	value > count
	@ $mol_prop()
	counter( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_number().setup( __ => { 
			__.hint = () => this.countHint()
			__.value = ( ...diff : any[] ) => this.count(  ...diff )
		} )
	}

	/// progress 0.33
	progress() {
		return 0.33
	}

	/// progresser $mol_portioner portion < progress
	@ $mol_prop()
	progresser( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_portioner().setup( __ => { 
			__.portion = () => this.progress()
		} )
	}

	/// publishLabel @ \Shared
	publishLabel() {
		return this.text( "publishLabel" )
	}

	/// publish false
	@ $mol_prop()
	publish( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : false
	}

	/// publisher $mol_checker 
	/// 	childs / < publishLabel 
	/// 	checked > publish
	@ $mol_prop()
	publisher( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_checker().setup( __ => { 
			__.childs = () => [].concat( this.publishLabel() )
			__.checked = ( ...diff : any[] ) => this.publish(  ...diff )
		} )
	}

	/// dropLabel @ \Drop
	dropLabel() {
		return this.text( "dropLabel" )
	}

	/// eventLog null
	@ $mol_prop()
	eventLog( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : <any> null
	}

	/// buttonDrop $mol_clicker_minor 
	/// 	childs / < dropLabel 
	/// 	eventClick > eventLog
	@ $mol_prop()
	buttonDrop( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_clicker_minor().setup( __ => { 
			__.childs = () => [].concat( this.dropLabel() )
			__.eventClick = ( ...diff : any[] ) => this.eventLog(  ...diff )
		} )
	}

	/// childs / 
	/// 	< titler 
	/// 	< counter 
	/// 	< progresser 
	/// 	< publisher 
	/// 	< buttonDrop
	childs() {
		return [].concat( this.titler() , this.counter() , this.progresser() , this.publisher() , this.buttonDrop() )
	}

} }

