module $ { export class $mol_pager extends $mol_viewer {

	/// title \
	title() {
		return ""
	}

	/// titler $mol_viewer childs / < title
	@ $mol_prop()
	titler( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_viewer().setup( __ => { 
			__.childs = () => [].concat( this.title() )
		} )
	}

	/// head / < titler
	head() {
		return [].concat( this.titler() )
	}

	/// header $mol_viewer childs < head
	@ $mol_prop()
	header( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_viewer().setup( __ => { 
			__.childs = () => this.head()
		} )
	}

	/// body null
	body() {
		return <any> null
	}

	/// bodier $mol_scroller childs / < body
	@ $mol_prop()
	bodier( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_scroller().setup( __ => { 
			__.childs = () => [].concat( this.body() )
		} )
	}

	/// foot null
	foot() {
		return <any> null
	}

	/// footer $mol_viewer childs / < foot
	@ $mol_prop()
	footer( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_viewer().setup( __ => { 
			__.childs = () => [].concat( this.foot() )
		} )
	}

	/// childs / 
	/// 	< header 
	/// 	< bodier 
	/// 	< footer
	childs() {
		return [].concat( this.header() , this.bodier() , this.footer() )
	}

} }

