module $ { export class $mol_icon extends $mol_svg {

	/// viewBox \0 0 48 48
	viewBox() {
		return "0 0 48 48"
	}

	/// attr * viewBox < viewBox
	attr() {
		return $mol_merge_dict( super.attr() , {
			"viewBox" : ()=> <any> this.viewBox() ,
		} )
	}

	/// path \
	path() {
		return ""
	}

	/// pather $mol_svg_path geometry < path
	@ $mol_prop()
	pather( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_svg_path().setup( __ => { 
			__.geometry = () => this.path()
		} )
	}

	/// childs / < pather
	childs() {
		return [].concat( this.pather() )
	}

} }

