module $ { export class $mol_labeler extends $mol_viewer {

	/// title \
	title() {
		return ""
	}

	/// titler $mol_viewer childs / < title
	@ $mol_prop()
	titler( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_viewer().setup( __ => { 
			__.childs = () => [].concat( this.title() )
		} )
	}

	/// content null
	content() {
		return <any> null
	}

	/// contenter $mol_viewer childs / < content
	@ $mol_prop()
	contenter( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_viewer().setup( __ => { 
			__.childs = () => [].concat( this.content() )
		} )
	}

	/// childs / 
	/// 	< titler 
	/// 	< contenter
	childs() {
		return [].concat( this.titler() , this.contenter() )
	}

} }

