module $ { export class $mol_stringer_demo extends $mol_rower {

	/// name \
	@ $mol_prop()
	name( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : ""
	}

	/// one $mol_stringer value > name
	@ $mol_prop()
	one( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_stringer().setup( __ => { 
			__.value = ( ...diff : any[] ) => this.name(  ...diff )
		} )
	}

	/// twoHint @ \Batman
	twoHint() {
		return this.text( "twoHint" )
	}

	/// two $mol_stringer 
	/// 	value > name 
	/// 	hint < twoHint
	@ $mol_prop()
	two( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_stringer().setup( __ => { 
			__.value = ( ...diff : any[] ) => this.name(  ...diff )
			__.hint = () => this.twoHint()
		} )
	}

	/// threeHint @ \Jocker
	threeHint() {
		return this.text( "threeHint" )
	}

	/// three $mol_stringer 
	/// 	value > name 
	/// 	hint < threeHint 
	/// 	disabled true
	@ $mol_prop()
	three( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_stringer().setup( __ => { 
			__.value = ( ...diff : any[] ) => this.name(  ...diff )
			__.hint = () => this.threeHint()
			__.disabled = () => true
		} )
	}

	/// childs / 
	/// 	< one 
	/// 	< two 
	/// 	< three
	childs() {
		return [].concat( this.one() , this.two() , this.three() )
	}

} }

