module $ { export class $mol_demo extends $mol_viewer {

	/// name \$mol_viewer
	name() {
		return "$mol_viewer "
	}

	/// title < name
	title() {
		return this.name()
	}

	/// titler $mol_viewer childs / < title
	@ $mol_prop()
	titler( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_viewer().setup( __ => { 
			__.childs = () => [].concat( this.title() )
		} )
	}

	/// widget null
	widget() {
		return <any> null
	}

	/// screener $mol_viewer childs / < widget
	@ $mol_prop()
	screener( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_viewer().setup( __ => { 
			__.childs = () => [].concat( this.widget() )
		} )
	}

	/// childs / 
	/// 	< titler 
	/// 	< screener
	childs() {
		return [].concat( this.titler() , this.screener() )
	}

} }

