module $ { export class $mol_labeler_demo extends $mol_rower {

	/// providerLabel @ \Provider
	providerLabel() {
		return this.text( "providerLabel" )
	}

	/// providerName @ \ACME Provider Inc.
	providerName() {
		return this.text( "providerName" )
	}

	/// labeler1 $mol_labeler 
	/// 	title < providerLabel 
	/// 	content < providerName
	@ $mol_prop()
	labeler1( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_labeler().setup( __ => { 
			__.title = () => this.providerLabel()
			__.content = () => this.providerName()
		} )
	}

	/// costLabel @ \Cost
	costLabel() {
		return this.text( "costLabel" )
	}

	/// labeler2 $mol_labeler 
	/// 	title < costLabel 
	/// 	content $mol_coster value $mol_unit_money_usd valueOf 1234567
	@ $mol_prop()
	labeler2( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_labeler().setup( __ => { 
			__.title = () => this.costLabel()
			__.content = () => new $mol_coster().setup( __ => { 
			__.value = () => new $mol_unit_money_usd().setup( __ => { 
			__.valueOf = () => 1234567
		} )
		} )
		} )
	}

	/// consumerLabel @ \Consumer
	consumerLabel() {
		return this.text( "consumerLabel" )
	}

	/// consumerName @ \ACME Consumer Inc.
	consumerName() {
		return this.text( "consumerName" )
	}

	/// labeler3 $mol_labeler 
	/// 	title < consumerLabel 
	/// 	content < consumerName
	@ $mol_prop()
	labeler3( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_labeler().setup( __ => { 
			__.title = () => this.consumerLabel()
			__.content = () => this.consumerName()
		} )
	}

	/// remarkLabel @ \Remark
	remarkLabel() {
		return this.text( "remarkLabel" )
	}

	/// remarkValue @ \Remind me later
	remarkValue() {
		return this.text( "remarkValue" )
	}

	/// labeler4 $mol_labeler 
	/// 	title < remarkLabel 
	/// 	content $mol_stringer value < remarkValue
	@ $mol_prop()
	labeler4( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_labeler().setup( __ => { 
			__.title = () => this.remarkLabel()
			__.content = () => new $mol_stringer().setup( __ => { 
			__.value = () => this.remarkValue()
		} )
		} )
	}

	/// childs / 
	/// 	< labeler1 
	/// 	< labeler2 
	/// 	< labeler3 
	/// 	< labeler4
	childs() {
		return [].concat( this.labeler1() , this.labeler2() , this.labeler3() , this.labeler4() )
	}

} }

