module $ { export class $mol_demo_all extends $mol_viewer {

	/// name \$mol_viewer
	name() {
		return "$mol_viewer"
	}

	/// mediumLabel \Fit to content
	mediumLabel() {
		return "Fit to content"
	}

	/// medium $mol_demo_medium 
	/// 	name < name 
	/// 	title < mediumLabel
	@ $mol_prop()
	medium( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_demo_medium().setup( __ => { 
			__.name = () => this.name()
			__.title = () => this.mediumLabel()
		} )
	}

	/// smallLabel @ \Minimum screen
	smallLabel() {
		return this.text( "smallLabel" )
	}

	/// small $mol_demo_small 
	/// 	name < name 
	/// 	title < smallLabel
	@ $mol_prop()
	small( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_demo_small().setup( __ => { 
			__.name = () => this.name()
			__.title = () => this.smallLabel()
		} )
	}

	/// largeLabel @ \Maximize to screen
	largeLabel() {
		return this.text( "largeLabel" )
	}

	/// large $mol_demo_large 
	/// 	name < name 
	/// 	title < largeLabel
	@ $mol_prop()
	large( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_demo_large().setup( __ => { 
			__.name = () => this.name()
			__.title = () => this.largeLabel()
		} )
	}

	/// childs / 
	/// 	< medium 
	/// 	< small 
	/// 	< large
	childs() {
		return [].concat( this.medium() , this.small() , this.large() )
	}

} }

