module $ { export class $mol_coder extends $mol_viewer {

	/// value \
	@ $mol_prop()
	value( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : ""
	}

	/// format \
	format() {
		return ""
	}

	/// hint < format
	hint() {
		return this.format()
	}

	/// manualer $mol_stringer 
	/// 	value > value 
	/// 	hint < hint
	@ $mol_prop()
	manualer( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_stringer().setup( __ => { 
			__.value = ( ...diff : any[] ) => this.value(  ...diff )
			__.hint = () => this.hint()
		} )
	}

	/// eventScan null
	@ $mol_prop()
	eventScan( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : <any> null
	}

	/// labelScan @ \Scan
	labelScan() {
		return this.text( "labelScan" )
	}

	/// scanner $mol_clicker 
	/// 	eventClick > eventScan 
	/// 	childs / < labelScan
	@ $mol_prop()
	scanner( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : new $mol_clicker().setup( __ => { 
			__.eventClick = ( ...diff : any[] ) => this.eventScan(  ...diff )
			__.childs = () => [].concat( this.labelScan() )
		} )
	}

	/// childs / 
	/// 	< manualer 
	/// 	< scanner
	childs() {
		return [].concat( this.manualer() , this.scanner() )
	}

} }

